body {
  margin: 0;
  font-family: Roboto, sans-serif;
}

:root {
  --blue-color: #00acdc;
}

.header {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 60px;
  display: flex;
}

.logo {
  width: 40px;
  height: 40px;
  margin: 10px 0 10px 50px;
}

.main-nav {
  justify-content: flex-end;
  margin-right: 50px;
  display: flex;
}

.nav-list {
  margin: 0;
  padding: 0;
  display: flex;
}

.nav-item {
  margin-right: 20px;
  padding: 0;
  list-style-type: none;
}

.nav-item:first-child {
  margin-left: 0;
}

.nav-item:last-child {
  margin-right: 0;
}

a {
  color: #000;
  margin: 22.5px 0;
  padding: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
}

a:hover {
  color: var(--blue-color);
}

.nav-item a {
  text-transform: uppercase;
}

.is-active {
